import {
	mapState
} from 'vuex'
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			type:0,
			pageNum: 1,
			productData: [],
			loadding: true,
			pullUpOn: true,
			notFound: false,
			canReachBottom: true,
			listData: [],
			searchData: {
				orderState: '',
				accountId: '',
				pageNum: 1,
				totalPage: null,
				query: ''
			},
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			referrerPay: false
		};
	},
	watch: {
		searchData:  {
			handler(val, newval){
				this.searchData.orderState=val.orderState
				this.searchData.query=val.query
			},
			deep: true
		}
	},
	methods: {
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		transUserState(num){
			switch(num){
				case 0:
					return [];
					break;
				case 1:
					return [0];
					break;
				case 2:
					return [1, 2];
					break;
				case 3:
					return [3];
					break;
				case 4:
					return [7];
					break;
				default:
					return [];
			}
		},
		transOrderState(num){
			switch(num){
				case 0:
					return '未支付';
					break;
				case 1:
					return '未发货';
					break;
				case 2:
					return '已发货';
					break;
				case 3:
					return '已完成';
					break;
				case 4:
					return '已退货';
					break;
				case 5:
					return '已退款';
					break;
				case 6:
					return '已取消';
					break;
				case 7:
					return '退款中';
					break;
				case 9:
					return '备货中';
					break;
				default:
					return '';
			}
		},
		doQuery(orderType){
			this.goTop();
			this.searchData.pageNum=1;
			this.type=orderType;
			this.loadData(this.transUserState(orderType));
			uni.setStorageSync('order_searchType_storage', this.transUserState(orderType));
		},
		handleSearch(){
			this.goTop();
			this.searchData.pageNum=1;
			this.type=0;
			this.$nextTick(() => {
				this.loadData(this.transUserState(0));
			});
			uni.setStorageSync('order_searchType_storage', this.transUserState(0));
		},
		loadData(orderState){
			this.loadding = true;
			this.pullUpOn = true;
			this.searchData={
				...this.searchData,
				orderState: orderState || []
			}
			this.$http.post('/mall/app/order/list', {
				...this.baseInfo,
				...this.searchData,
				accountId: this.userInfo && this.userInfo.accountId || '',
			})
			.then( res => {
				console.log(res)
				if(res.code == 0){
					if(res.result){
						this.searchData.totalPage=res.result.pages;
						if(res.result.records.length == 0){
							this.loadding = false;
							this.pullUpOn = false;
						}else{
							this.loadding = false;
						}
						if(this.searchData.pageNum == 1){
							if(res.result.records){
								this.listData=res.result.records;
							}
						}else{
							this.listData=this.listData.concat(res.result.records);
						}
						this.listData.map(item => {
							this.$set(item, 'orderStateName', this.transOrderState(item.orderlistState));
						})
					}
					
				}else{
					console.log("product.vue-- list获取数据列表失败");
				}
			})
			.catch( err => {
				console.log("product.vue-- list获取数据列表错误");
			})
		},
		
		
		// 跳转详情
		linkToDetail(orderId){
			uni.navigateTo({
				url: '/pages/orderDetail/orderDetail?orderId=' + orderId
			})
		},
		//取消订单
		cancelOrder(orderId){
			uni.showModal({
			    title: '提示',
			    content: '确定取消订单吗？',
				confirmColor: '#E93B3D',
			    success: res => {
			        if (res.confirm) {
			            this.$http.post('/mall/app/order/cancel', {
							...this.baseInfo,
							accountId: this.userInfo.accountId,
							orderId: orderId
						})
						.then( res => {
							if(res.code == 0){
								this.loadData(uni.getStorageSync('order_searchType_storage'));
								alert('取消成功')
								uni.showToast({
									icon: 'none',
									title: '取消订单成功'
								})
								
							}else{
								uni.showToast({
									icon: 'none',
									title: '取消订单失败'
								})
								// this.loadData(uni.getStorageSync('order_searchType_storage'));
							}
						})
						.catch( err => {
							console.log(err);
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 去付款
		linkToPayment(order){
			window.location.href=`http://${this.paymentUrl}.yujianli.cn/#/pages/payment/cashRegister?orderNo=`+order.orderlistNum+"&mallDomain="+this.baseInfo.mallDomain+'&orderPayPrice='+order.payPrice+'&token='+encodeURIComponent(this.token);
		},
		// 查看物流详情
		linkToDelivery(orderId){
			uni.navigateTo({
				url: '/pages/delivery/delivery?orderId=' + orderId
			})
		},
		// 确认收货
		confirmOrder(orderId){
			this.$http.post('/mall/app/order/receive', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				orderId: orderId
			})
			.then( res => {
				if(res.code == 0){
					uni.showToast({
						icon: 'none',
						title: '确认收货成功'
					})
					this.loadData(uni.getStorageSync('order_searchType_storage'));
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		//删除订单
		deleteOrder(index){
			// uni.showLoading({
			// 	title: '请稍后'
			// })
			// setTimeout(()=>{
			// 	this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
			// 	uni.hideLoading();
			// }, 600)
		},
		
	},
	onShow(){
		let orderReferrer = document.referrer;
		if(orderReferrer.indexOf('pay') !== -1){
			this.referrerPay=true;
		}
	},
	onLoad(options){
		this.loadData(this.transUserState(Number(options.state)));
		this.type=options.state;
		uni.setStorageSync('order_searchType_storage', this.transUserState(Number(options.state)));
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.searchData.pageNum++;
		this.loadData(uni.getStorageSync('order_searchType_storage'));
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo', 'paymentUrl', 'token'])
	}
}
